import { useEffect, useState, useMemo } from "react";
import { sendRequest } from "../api/helpers";

function Middle({ input }) {
  const [data, setData] = useState({});
  const [categories, setCategories] = useState("ALL");

  useEffect(() => {
    const controller = new AbortController();
    const abort = controller.signal;

    (async () => {
      const { contents } = await sendRequest(
        `app-contents/jVV3Q?appId=2731&eventId=2570`,
        abort,
        1
      );
      const result = [...contents];
      result.pop();
      const obj = {};
      result.forEach((item) => {
        obj[item.catName] = item;
      });
      setData(obj);
    })();

    return () => controller.abort();
  }, []);

  const correctInfo = useMemo(() => {
    let result = { ...data };
    if (input.length > 1) {
      let arr = [];
      Object.values(result).forEach((item) => {
        item.contentInf.forEach(item => {
          if (item.name.toUpperCase().includes(input.toUpperCase())) arr.push(item);
        });
      });
      return arr;
    }
    if (categories === "ALL") {
      let arr = [];
      Object.values(result).forEach((item) => {
        item.contentInf.forEach((item) => arr.push(item));
      });
      return arr;
    }
    return result[categories].contentInf;
  }, [categories, data, input]);

  console.log(correctInfo, "correctInfo");
  console.log(data, "data");

  const title = categories !== "ALL" ? <p>{data[categories].catName}</p> : null;
  const descripion =
    categories !== "ALL" ? <p>{data[categories].description}</p> : null;

  return (
    <section className="middle__section">
      <div className="middle__categories categories">
        <div className="categories__nav">
          <ul>
            <li></li>
            <li onClick={() => setCategories("ALL")}>
              <p>{"ALL"}</p>
            </li>
            {Object.keys(data).map((item, index) => {
              return (
                <li key={index} onClick={() => setCategories(item)}>
                  <p>{item}</p>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
      <div className="middle__mainInfo mainInfo">
        <div className="mainInfo__titles">
          {title}
          {descripion}
        </div>
        <div className="mainInfo__items">
          <div className="mainInfo__items-wrapper row15">
            {correctInfo.map((item) => {
              return (
                <div
                  key={item.id}
                  className="mainInfo__item item__wrapper col15"
                >
                  <div className="item">
                    <a href={item.download_link} target="_blank">
                      <div className="item__img-wrapper">
                        <div className="item__img">
                          <img src={item.image} alt="" />
                        </div>
                        <div className="item__title">
                          <p>{item.name}</p>
                        </div>
                      </div>
                    </a>
                    <div className="item__button">
                      <button>ADD</button>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}

export default Middle;
